.fixed-btns {
	position: fixed;
	top: 20%;
	right: 0;
	z-index: 20;
	display: flex;
	flex-direction: column;
	img {
		width: 90px;
		margin-bottom: 10px; } }
.home-banner-section {
	.banner-video {
		position: absolute;
		top: 110px;
		left: 0;
		z-index: 2;
		width: 100%;
		.banner-video-player {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			&._playing ~ .play-btn {
				opacity: 0; }
			.video-js {
				width: 100%;
				height: 100%;
				position: absolute; } }
		.fixed-thumb-wrapper {
			border: 5px solid #8F2064;
			border-radius: 16px;
			overflow: hidden;
			width: 628px;
			margin-left: auto;
			margin-right: 0;
			position: relative;
			.play-btn {
				position: absolute;
				cursor: pointer;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				z-index: 1;
				width: 56px;
				height: 56px;
				background: #F2F2F2;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				svg {
					transform: translateX(2px);
					width: 17px;
					height: 19px;
					fill: #8F2064; } } } }
	.img {
		display: table;
		margin-left: auto;
		margin-right: auto;
		padding-top: 56px; }
	.bg-img {
		position: relative;

		.l-1 {
			position: relative; }
		.l-2, .l-3, .l-4 {
			position: absolute;
			top: 0;
			left: 0; }
		.l-2 {
			top: 15%;
			left: 7%;
			max-width: 340px; }
		.l-3 {
			top: 30%;
			left: 10%;
			max-width: 400px; } }
	.count-down {
		background: #FFE9E8;
		border-radius: 28px;
		border: 5px solid #CF3393;
		width: 650px;
		padding: 10px;
		padding-right: 20px;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		justify-content: space-between;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		&.done {
			display: none; }
		.count-down-box {
			position: relative;
			.number, .type {
				position: absolute;
				font-size: 50px;
				font-weight: 800;
				letter-spacing: 2px;
				color: #fff; }
			.number {
				top: 35%;
				left: 55%;
				transform: translate(-50%, -50%); }
			.type {
				top: 75%;
				left: 55%;
				font-size: 25px;
				transform: translate(-50%, -50%); } } }
	&:before {
		position: absolute;
		content: '';
		bottom: 0;
		width: 100%;
		height: 30%;
		background: #8e1161; }
	.img {
		img {
			display: block;
			margin-left: auto;
			margin-right: auto; } }

	.end-text {
		position: relative;
		background: #8e1161;
		padding-top: 24px;
		padding-bottom: 30px;
		p {
			text-align: center;
			color: #fff;
			width: 672px;
			max-width: 100%;
			margin-left: auto;
			margin-right: auto; } }
	.bg-end {
		position: absolute;
		bottom: 0;
		width: 100%;
		padding-bottom: 20.68%;
		background-position: bottom center; } }

.home-banner-section.count-down-done {
	.end-text {
		margin-top: -3%; } }
.feature-section {
	background-color: #F9C9DE;
	padding-top: 25px;
	padding-bottom: 85px; }


.feature-box {
	background: #FDE4EF;
	border-radius: 10px;
	border: 5px solid #FFEDF6;
	padding: 5px;
	position: relative;
	.img {
		border-radius: 5px;
		overflow: hidden;
		margin-bottom: 25px;
		.fixed-thumb {
			padding-bottom: 75.7668%; } }
	.icon {
		position: absolute;
		top: 15px;
		right: 15px;
		max-width: 65px; }

	.text {
		text-align: center;
		padding: 0 20px;
		h4 {
			font-size: 17px;
			color: #BC3698;
			font-weight: 800;
			font-family: $font-2;
			text-transform: uppercase;
			line-height: 1.5; }
		p {
			width: 297px;
			max-width: 100%;
			font-family: $font-2; } } }

.feature-swiper {
	.swiper-slide {
		display: flex;
		justify-content: center;
		> * {
			width: 100%;
			max-width: 360px; } } }


.feature-swiper-wrapper {
	position: relative; }
.feature-nav {
	z-index: 2;
	cursor: pointer;
	background: rgba(#FFFFFF, .4);
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 64px;
	height: 64px;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	&:focus {
		outline: 0; }
	&:hover {
		background: #fff;
		svg {
			fill: #BC3698; } }
	&._prev {
		right: calc(100% + 30px); }
	&._next {
		left: calc(100% + 30px); }


	svg {
		width: 12px;
		height: 24px;
		fill: #333; } }

.event-section {
	padding-top: 55px;
	padding-bottom: 85px;
	background: linear-gradient(180deg, #FAE4EE 0%, #FADAE8 48.24%, rgba(249, 201, 222, 0) 131.93%);
	overflow: hidden;
	.title__ {
		margin-bottom: 40px; }
	.event-light-bg {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 1140px;
		img {
			width: 2000px; } } }
.event-box {
	position: relative;
	border-radius: 4px;
	margin-bottom: 10px;
	box-shadow: 0px 0px 18px rgba(0, 0, 0, 0.1);
	.fixed-thumb {
		border-radius: 4px;
		padding-bottom: 72.043%;
		&._2 {
			padding-bottom: 146.049%; }
		&._3 {
			padding-bottom: 84.77%; } } }
.event-grid {
	margin-left: -5px;
	margin-right: -5px;
	> [class*="col-"] {
		padding-left: 5px;
		padding-right: 5px; } }

.launch-section {
	background-color: #F9C9DE;
	padding-top: 55px;
	padding-bottom: 85px; }
.launch-row {
	margin-left: -12px;
	margin-right: -12px;
	> [class*="col-"] {
		padding-left: 12px;
		padding-right: 12px; } }
.launch-box {
	border-radius: 8px;
	margin-bottom: 24px;
	.fixed-thumb {
		border-radius: 8px;
		padding-bottom: 123.061%;
		&._2 {
			padding-bottom: 78.57%; } } }

.manual-section {
	background: linear-gradient(215.83deg, #8E1F63 6.2%, #CF3393 96.35%);
	padding: 15px 0;
	.manual-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		pointer-events: none; } }
.manual-content {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 0 auto;
	width: 1230px;
	max-width: 100%;
	padding: 0 15px;
	.img-left {
		max-width: 45%;
		.text {
			margin-top: 20px;
			font-size: 17px;
			font-weight: 600;
			color: #fff;
			text-transform: uppercase;
			display: table;
			margin-left: auto;
			margin-right: auto;
			padding: 10px 30px;
			position: relative;
			&:before, &:after {
				position: absolute;
				content: '';
				width: 100%;
				height: 2px;
				// background: #fff
				top: 0;
				left: 0;
				background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.7497373949579832) 12%, rgba(255,255,255,1) 24%, rgba(255,255,255,1) 48%, rgba(255,255,255,0.9654236694677871) 75%, rgba(255,255,255,0.7077205882352942) 88%, rgba(255,255,255,0) 100%); }
			&:after {
				top: auto;
				bottom: 0; } } }
	.img-right {
		max-width: 45%; } }

.utility-section {
	overflow: hidden;
	background-color: #FFEDF6;
	padding-top: 45px;
	padding-bottom: px;
	.bg {
		position: absolute;
		top: 10%;
		left: 0;
		width: auto;
		opacity: 0.05;
		&._2 {
			right: 0;
			left: auto; } } }

.utility-box {
	display: table;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 40px;
	img {
		margin-bottom: 16px; }
	p {
		font-size: 19px;
		font-weight: 600;
		font-family: $font-2;
		text-align: center; } }


.welcome-section {
	padding-top: 60px;
	padding-bottom: 80px;
	.section-title {
		display: table;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 70px; }

	h3 {
		font-size: 28px;
		font-weight: bold;
		color: #B41E8D;
		text-align: center;
		margin-bottom: 26px; }
	.map {
		margin-bottom: 88px; }
	.map-row {
		margin-left: -7px;
		margin-right: -7px;
		> [class*="col-"] {
			padding-left: 7px;
			padding-right: 7px; } }
	.map-box {
		img {
			display: block;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 34px; }
		h5 {
			color: #B41E8D;
			font-weight: 900;
			font-size: 18px;
			font-family: $font-2;
			text-transform: uppercase;
			text-align: center; }
		p {
			text-align: center;
			font-family: $font-2;
			color: #212121;
			font-weight: 600;
			width: 300px;
			margin-left: auto;
			margin-right: auto; } } }

@media (max-width: 1199.82px) {
	.home-banner-section {
		.banner-video {
			.fixed-thumb-wrapper {
				width: 500px;
				max-width: 100%; } }
		.img {
			min-height: 700px; } } }
@media (max-width: 991.82px) {

	.fixed-btns {
		img {
			max-width: 60px; } }

	.home-banner-section {
		&:before {
			height: 60%; }
		.banner-video {
			position: relative;
			top: auto;
			.fixed-thumb-wrapper {
				margin-left: auto;
				margin-right: auto; } }
		.img {

			margin-bottom: 20px;
			min-height: unset;
			img {} }
		.bg-img {
			.l-2 {
				width: 250px;
				top: 20%; }
			.l-3 {
				top: 30%;
				width: 300px; } }
		.count-down {
			width: 400px;
			.count-down-box {
				img {
					width: 100px; }
				.number {
					font-size: 28px; }
				.type {
					font-size: 13px; } } } }

	.manual-content {
		flex-direction: column;
		.img-left {
			width: 395px;
			max-width: 100%; }
		.img-right {
			width: 395px;
			max-width: 100%; } } }
@media (max-width: 575.82px) {
	.fixed-btns {
		img {
			max-width: 40px; } }
	.feature-nav {
		width: 40px;
		height: 40px;
		svg {
			width: 8px;
			height: 16px; }
		&._next {
			right: 0;
			left: auto; }
		&._prev {
			left: 0;
			right: auto; } }

	.home-banner-section {
		overflow: hidden;
		.img {
			padding-top: 0;
			padding-bottom: 40px; }
		.count-down {
			bottom: 0px;
			padding: 5px;
			padding-right: 10px;
			width: 300px;
			.count-down-box {
				img {
					width: 70px; } } }
		.img {}
		.bg-img {
			.l-2 {
				left: 25%;
				width: 150px; }
			.l-3 {
				width: 120px;
				top: 32%;
				left: 20%; } } }
	.home-banner-section.count-down-done {
		background: #8d1061;
		.img {
			padding-bottom: 0;
			margin-bottom: 0; } }


	.manual-content .img-left .text {
		font-size: 10px; } }

@media (max-width: 420.82px) {
	.home-banner-section {
		.bg-img {
			.l-2 {
				top: 25%; }
			.l-3 {
				top: 32%;
				left: 64%;
				width: 140px; } }
		.end-text p {
			font-size: 12px; } } }
