@mixin glam-title {
	font-size: 32px;
	margin-bottom: 20px;
	font-weight: 800;
	text-transform: uppercase;
	color: #333; }
.glam-page {
	font-family: $font-3; }
.glam-banner-swiper-nav {
	position: absolute;
	top: 50%;
	z-index: 1;
	transform: translateY(-50%);
	cursor: pointer;
	svg {
		width: 20px;
		fill: rgba(#333, .5);
		transition: all .3s ease-in-out; }
	&.prev {
		left: 15px; }
	&.next {
		right: 15px; }
	&:hover {
		svg {
			fill: #333; } } }


.glam-banner {
	position: relative;
	height: 80vh;
	.video-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding-bottom: 0;
		height: 100%;
		display: none;
		.video-js {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%; }
		&._playing {
			display: block; }
		&._playing + .play-btn {
			display: none; } }
	.play-btn {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background: #fff;
		cursor: pointer;
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 50%;
			transform: translate(-30%, -50%);
			border-left: 15px solid #333;
			border-top: 10px solid transparent;
			border-bottom: 10px solid transparent; } } }
.glam-intro-section {
	overflow: hidden;
	padding-top: 115px;
	padding-bottom: 115px;
	h3 {
		@include glam-title; }

	.about-intro {
		position: absolute;
		top: 0;
		right: 0;
		transform: translateY(-30%);
		&._2 {
			right: auto;
			left: 0;
			transform: translateY(-30%) scale(-1, 1); } } }

.glam-services-section {
	h3 {
		@include glam-title;
		margin-bottom: 97px; } }
.g-service-box {
	margin-bottom: 125px;
	.img-col {
		z-index: 1;
		display: block; }
	.img {
		background: #fff;
		padding: 10px;
		transform: translateY(-10%);
		position: relative; }
	.text {
		background: #ffe8f2;
		padding: 70px 44px;
		position: relative;
		text-align: justify;
		&:before {
			position: absolute;
			content: '';
			width: 30%;
			height: 100%;
			background: #ffe8f2;
			right: 100%;
			top: 0; }
		h4 {
			font-size: 35px;
			font-weight: 800;
			color: #333;
			text-transform: uppercase;
			margin-bottom: 25px; }
		p {}
		.deco {
			position: absolute;
			top: 100%;
			right: 0;
			transform: translate(50%, -50%); } }
	&._reverse {
		.text {
			background: #ffe9da; }
		.text:before {
			right: auto;
			left: 100%;
			background: #ffe9da; }
		.text .deco {
			right: auto;
			left: 0;
			transform: translate(-50%, -50%) scale(-1, 1); } } }


.glam-brands-section {
	padding-bottom: 86px;
	.title-wrapper {
		position: relative;
		margin-bottom: 36px;
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 0;
			width: 100%;
			height: 1px;
			background: #DBBFB9; } }

	h3 {
		@include glam-title;
		margin-bottom: 0;
		background: #fff;
		padding-left: 30px;
		padding-right: 30px;
		position: relative;
		margin-left: auto;
		margin-right: auto;
		display: table; } }


.brand-tablist {
	margin-bottom: 55px;
	position: relative;
	display: flex;
	justify-content: center;
	li {
		a {
			display: block;
			padding: 0 30px;
			padding-bottom: 14px;
			border-bottom: 1px solid #C4C4C4;
			font-size: 14px;
			font-weight: 800;
			text-transform: uppercase;
			color: #C4C4C4;
			&.active {
				border-bottom-color: transparent;
				color: #B12F84; } } } }
.brand-tab-content {
	position: relative; }
.brand-box {
	img {
		display: block;
		margin-left: auto;
		margin-right: auto; } }
.brand-swiper-nav {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	cursor: pointer;
	> svg {
		width: 10px;
		height: 22px;
		fill: #171717; }

	&.prev {
		right: calc(100% + 10px); }
	&.next {
		left: calc(100% + 10px); } }
.brand-swiper {
	.swiper-slide {
		display: flex;
		align-items: center;
		justify-content: center; } }
.glam-wellness-section {
	padding-top: 92px;
	background: #FEF6FA;
	padding-bottom: 210px;
	.img {
		position: relative;
		.text {
			transform: translate(30px, 55%);
			position: absolute;
			padding: 20px;
			bottom: 0;
			right: 0;
			background: #F995B3;
			max-width: 100%;
			width: 741px;
			text-align: justify;
			h4 {
				font-size: 32px;
				color: #fff;
				font-weight: 800;
				text-transform: uppercase;
				margin-bottom: 18px; }

			p {
				font-size: 15px;
				color: #fff; } } } }


.glam-product-section {
	background: #FEF6FA;
	overflow: hidden;
	.title-wrapper {
		position: relative;
		margin-bottom: 36px;
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 0;
			width: 100%;
			height: 1px;
			background: #AFAFAF; } }

	h3 {
		@include glam-title;
		margin-bottom: 0;
		background: #FEF6FA;
		padding-left: 30px;
		padding-right: 30px;
		position: relative;
		margin-left: auto;
		margin-right: auto;
		display: table;
		padding-top: 10px; } }

.glam-product-box {
	.img-col {
		z-index: 1; }
	.img {
		width: 100%;
		z-index: 1;
		img {
			transform: translateX(-20%);
			width: 130%;
			max-width: unset;
 } }			// transform: translateX(-20%)
	.text {
		position: relative;
		background: #fff;
		padding: 62px;
		padding-right: 40px;
		margin-top: 100px;
		> * {
			position: relative;
			z-index: 1; }
		&:before, &:after {
			z-index: 0;
			position: absolute;
			content: '';
			top: 0;
			right: 100%;
			height: 100%;
			background: #fff;
			width: 20%; }
		&:after {
			width: 50vw;
			left: 100%;
			right: auto; }

		h4 {
			max-width: 100%;
			width: 447px;
			font-size: 35px;
			color: #333;
			font-weight: 800;
			text-transform: uppercase;
			margin-bottom: 25px; }
		p {
			font-size: 14px;
			color: #333; } } }

.glam-product-detail {
	margin-top: 38px;
	[class*="col-"] {
		display: flex;
		border-right: 1px solid #E7E7E7;
		&:last-child {
			border-right: 0; }
		.img {
			flex-shrink: 0;
			width: 27px;
			margin-right: 10px; }
		.text {
			h5 {
				text-transform: uppercase;
				font-size: 14px;
				font-weight: bold;
				color: #333; }
			p {
				font-size: 14px; } } } }

.glam-product-2 {
	margin-top: 50px;
	.row {
		margin-left: -20px;
		margin-right: -20px;
		> [class*="col-"] {
			padding-left: 20px;
			padding-right: 20px; } }
	.text {
		position: relative;
		background: #fff;
		padding: 60px 90px;
		padding-left: 0;
		> * {
			position: relative; }
		&:before {
			position: absolute;
			content: '';
			top: 0;
			right: 0;
			width: 50vw;
			height: 100%;
			background: #fff; }

		h4 {
			font-size: 35px;
			font-weight: 800;
			color: #333;
			text-transform: uppercase; }
		p {
			font-size: 14px;
			color: #333; } }
	.img {
		width: 100%;
		img {
			width: 130%;
			max-width: unset; }
		p {
			color: #333;
			font-size: 14px;
			text-align: center; } } }

.glam-product-3 {
	padding-bottom: 80px;
	position: relative;
	.row-detail {
		> [class*="col-"] {
			border-right: 1px solid #e7e7e7;
			padding-top: 20px;
			&:last-child {
				border-right: 0; } } }
	&:before {
		position: absolute;
		content: '';
		bottom: 0;
		width: 100%;
		height: 35%;
		background: #fff; }
	.container {
		position: relative;
		padding-bottom: 30px;
		.bg {
			position: absolute;
			bottom: 0;
			left: 0;
			height: calc(100% / 3 * 2);
			width: 100%;
			border: 10px solid #fff;
			background: #EDFAF2; } }

	h4 {
		font-size: 28px;
		font-weight: 800;
		color: #333;
		text-transform: uppercase;
		margin-bottom: 15px; }

	p {
		font-size: 14px;
		color: #333; }

	.d-img {
		flex-shrink: 0;
		width: 27px;
		margin-right: 10px; }
	.d-text {
		h5 {
			font-size: 14px;
			font-weight: bold;
			color: #333;
			text-transform: uppercase; }
		p {
			font-size: 14px; } } }

.glam-members-section {
	background: linear-gradient(0deg, #842268 2.42%, #842268 2.56%, #CD3693 104.3%);
	padding-top: 64px;
	.member-deco {
		position: absolute;
		top: 20%;
		left: 5%;
		img {
			max-width: 200px; }
		&._2 {
			bottom: 20%;
			top: auto;
			right: 5%;
			left: auto; } }

	.member-logo {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		padding: 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		img {
			max-width: 90px; } }
	h3 {
		margin-bottom: 29px;
		font-size: 32px;
		font-weight: 800;
		color: #fff;
		text-transform: uppercase;
		text-align: center; }
	.end-img {
		position: relative; }
	.els {
		position: absolute;
		bottom: 20px;
		left: 0;
		width: 100%;
		&-content {
			display: flex;
			justify-content: flex-end;
			align-items: flex-end; }
		h4 {
			font-size: 16px;
			font-weight: 800;
			color: #212121;
			span {
				color: #B1268C; } }
		.store {
			margin-bottom: 15px;
			margin-right: 10px;
			flex-shrink: 0;
			img {
				max-width: 130px; } } } }

.glam-footer {}
.glam-f-start {
	padding: 25px 0;
	background: #212121;
	h4 {
		font-size: 13px;
		font-weight: 800;
		color: #fff; }
	p {
		font-size: 12px;
		color: #fff;
		margin-bottom: 0;
		&:first-child {
			margin-bottom: 10px; } }
	.col-border {
		border-left: 1px solid rgba(#fff, .3); } }
.glam-f-end {
	padding-top: 37px;
	background: #FFEDF6;
	[class*="col-"] {
		margin-bottom: 20px; }
	h4 {
		font-size: 13px;
		font-weight: 800;
		color: #212121; }
	p {
		font-size: 12px;
		color: #212121;
		margin-bottom: 10px;
		&:first-child {
			margin-bottom: 10px; } } }

@media (max-width: 991.82px) {
	.glam-services-section {
		h3 {
			margin-bottom: 30px; } }
	.g-service-box {
		margin-bottom: 100px;
		.text-col {
			order: 1; }
		.img-col {
			order: 2; }
		.img {
			transform: none;
			padding: 0;
			img {
				display: block;
				width: 100%; } }
		.text {
			padding: 30px;
			&:before {
				display: none; }
			h4 {
				font-size: 25px; } } }

	.glam-wellness-section {
		padding-bottom: 100px;
		.img {
			.text {
				position: static;
				transform: none;
				padding: 30px;
				h4 {
					font-size: 25px;
					font-weight: bold; } } } }

	.glam-product-detail [class*=col-] {
		border-right: 0; }

	.glam-product-box {
		.img {
			img {
				width: 100%;
				transform: none; } }
		.text {
			margin-top: 20px;
			h4 {
				font-size: 25px;
				width: 100%; } } }

	.glam-product-2 {
		.text {
			padding: 30px;
			&:before {
				display: none; }
			h4 {
				font-size: 25px;
				font-weight: bold; } }
		.img {
			img {
				width: 100%; } } }

	.glam-product-3 {
		h4 {
			font-size: 25px;
			font-weight: bold; } }

	.glam-members-section {
		h3 {
			br {
				display: none; } } }


	.glam-f-start {
		.logo {
			margin-bottom: 20px; } } }

@media (max-width: 767.82px) {
	.brand-tablist {
		margin-bottom: 30px;
		li {
			margin-bottom: 15px; } }
	.glam-members-section {
		h3 {
			font-size: 28px; } } }
@media (max-width: 575.82px) {
	.glam-intro-section {
		padding: 80px 0;
		h3 {
			font-size: 25px;
			font-weight: bold; }
		.about-intro {
			width: 150px; } }

	.glam-services-section {
		h3 {
			font-size: 25px;
			font-weight: bold; } }

	.g-service-box {
		.text {
			padding: 15px;
			h4 {
				font-size: 18px;
				font-weight: bold; } } }
	.glam-brands-section {
		h3 {
			font-size: 25px;
			font-weight: bold; } }
	.brand-swiper-nav {
		z-index: 2;
		&.prev {
			right: auto;
			left: 0; }
		&.next {
			left: auto;
			right: 0; } }

	.glam-wellness-section {
		padding-bottom: 50px;
		.img {
			.text {
				h4 {
					font-size: 18px;
					margin-bottom: 20px; } } } }

	.glam-product-section {
		h3 {
			padding: 0;
			font-size: 25px;
			font-weight: bold;
			text-align: center; } }

	.glam-product-box {
		.text {
			padding: 15px;
			h4 {
				font-size: 18px; } } }

	.glam-product-2 {
		margin-top: 30px;
		.text {
			padding: 15px;
			h4 {
				font-size: 18px; } } }
	.glam-product-3 {
		padding-bottom: 10px;
		h4 {
			font-size: 18px; } }


	.glam-members-section {
		padding-top: 100px;
		h3 {
			font-size: 25px; } }

	.glam-members-section {
		.end-img {
			height: 150px;
			&:before {
				position: absolute;
				content: '';
				background: #f4e0ec;
				height: 60%;
				left: 0;
				bottom: 0;
				width: 100%; } } } }




