$topvalu-color-1: #B41E8E;

@media (min-width: 1200px) {
	.col-xl-20 {
		flex: 0 0 20%;
		max-width: 20%; } }
.topvalu-page {
	p {
		font-family: $font-2; } }
.eshop-header._topvalu-ext {
	padding: 10px 0; }

.topvalu-title__ {
	h3 {
		font-size: 32px;
		font-weight: bold;
		color: #212121;
		text-transform: uppercase;
		margin-bottom: 0;
		span {
			color: $topvalu-color-1; }
		@media (max-width: 767.82px) {
			font-size: 25px; }
		@media (max-width: 575.82px) {
			font-size: 20px; } } }

.topvalu-intro-section {
	padding: 80px 0;
	.pattern {
		position: absolute;
		top: 0;
		left: 0;
		&._1 {
			right: 0;
			left: auto;
			bottom: 0;
			top: auto;
			transform: scale(-1); } }
	p {
		font-size: 17px; }
	.topvalu-title__ {
		margin-bottom: 20px; } }

.topvalu-commit-section {
	background: #FFFAEE;
	padding-top: 62px;
	padding-bottom: 10px;

	ul li {
		display: flex;
		align-items: center;
		margin-bottom: 22px;
		div:first-child {
			flex-shrink: 0;
			width: 42px;
			height: 42px;
			color: $topvalu-color-1;
			font-size: 14px;
			font-weight: bold;
			font-family: $font-2;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;
			border: 4px solid $topvalu-color-1;
			position: relative;
			margin-right: 20px;
			&:before {
				position: absolute;
				content: '';
				width: 38px;
				height: 38px;
				border-radius: 50%;
				border: 2px solid #972079;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%); } }
		div:last-child {
			font-weight: bold;
			color: #212121;
			font-size: 16px;
			font-family: $font-2; } } }


.topvalu-criterion-section {
	padding-top: 80px;
	padding-bottom: 80px;
	.topvalu-title__ {
		margin-bottom: 62px; } }

.criterion-list {
	width: 910;
	max-width: 100%;
	margin-left: auto;
	margin-right: auto; }
.criterion-accordion-list {
	padding-left: 140px;
	position: relative;
	&:before {
		position: absolute;
		content: '';
		height: 100%;
		width: 2px;
		background: $topvalu-color-1;
		top: 0;
		left: 0; } }
.criterion-accordion {
	background: #FFEFEF;
	border-radius: 12px;
	border: 0;
	margin-bottom: 25px;
	h2 {
		position: relative;
		button {
			width: 100%;
			text-align: left;
			font-size: 17px;
			font-weight: 800;
			color: $topvalu-color-1;
			padding: 15px 42px;
			padding-right: 102px;
			&:hover {
				text-decoration: none;
				color: $topvalu-color-1; }
			&:not(.collapsed) {
				~ .angle {
					transform: translate(50%, -50%) scale(1, -1); }
				~ .year {
					&:before {
						background: $topvalu-color-1; }
					&:after {
						background: #fff; } } } }
		.year {
			position: absolute;
			left: -140px;
			top: 50%;
			transform: translateY(-50%);
			font-size: 24px;
			font-weight: bold;
			color: $topvalu-color-1;
			padding-left: 29px;
			&:before {
				position: absolute;
				content: '';
				width: 18px;
				height: 18px;
				top: 50%;
				border-radius: 50%;
				background: transparent;
				left: 1px;
				transform: translate(-50%, -50%);
				transition: all .3s ease; }
			&:after {
				position: absolute;
				content: '';
				width: 10px;
				height: 10px;
				top: 50%;
				left: 1px;
				transform: translate(-50%, -50%);
				background: $topvalu-color-1;
				border-radius: 50%;
				transition: all .2s ease; } }
		.angle {
			position: absolute;
			right: 51px;
			top: 50%;
			transform: translate(50%, -50%);
			color: $topvalu-color-1;
			font-size: 20px; } }
	.accordion-text {
		padding-left: 42px;
		padding-right: 102px;
		padding-bottom: 32px; } }
.criterion-accordion-list .criterion-accordion {
	&:first-child {
		h2 {
			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: -140px;
				width: 3px;
				background: #fff;
				height: 50%; } } }
	&:last-child {
		&:before {
			position: absolute;
			content: '';
			background: #fff;
			height: 100%;
			width: 3px;
			left: -140px;
			top: 0; }
		h2 {
			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: -140px;
				width: 2px;
				height: 50%;
				background: $topvalu-color-1; } } } }


.topvalu-product-section {
	background: #FFFAEE;
	padding-bottom: 81px;
	padding-top: 62px;
	.pattern {
		position: absolute;
		top: 0;
		left: 0;
		&._1 {
			top: auto;
			left: auto;
			right: 0;
			bottom: 0; } }
	.container {
		position: relative; } }
.topvalu-product-box {
	text-align: center;
	font-weight: bold;
	color: #333; }
.topvalu-cate-row {
	> [class*="col-"] {
		display: flex; } }
.topvalu-cate-box {
	border-radius: 10px;
	display: flex;
	width: 100%;
	background: #fff;
	transition: all .3s ease-in-out;
	overflow: visible;
	box-shadow: 0 0 0 10px $topvalu-color-1;
	transform: scale(1.05);
	&._color-1 {
		box-shadow: 0 0 0 10px #AE007A; }
	&._color-2 {
		box-shadow: 0 0 0 10px #006B2C; }
	&._color-3 {
		box-shadow: 0 0 0 10px #FAC031; }
	&._color-4 {
		box-shadow: 0 0 0 10px #221815; }
	.wrap {
		width: 100%;
		display: flex;
		flex-direction: column;
		border-radius: 10px;
		transition: all .3s ease-in-out;
		box-shadow: 0px 0px 4px rgba(black, .2);
		> * {
			width: 100%; } }
	.img {
		padding: 33px;
		transition: all .3s ease-in-out;
		text-align: center; }
	._title {
		font-size: 23px;
		padding: 10px 0;
		font-weight: 800;
		background: $topvalu-color-1;
		color: #fff;
		text-align: center;
		transition: all .3s ease-in-out;
		&._color-1 {
			background: #AE007A; }
		&._color-2 {
			background: #006B2C; }
		&._color-3 {
			background: #FAC031; }
		&._color-4 {
			background: #221815; } }
	.text {
		transition: all .3s ease-in-out;
		text-align: center;
		padding: 17px;
		display: flex;
		align-items: center;
		flex-grow: 1;
		p {
			font-size: 19px;
			font-weight: 600;
			color: #333;
			margin-bottom: 0; } }
	&:not(:hover) {

		box-shadow: none!important;
		transform: none;
		.wrap {
			transform: translateY(50%);
			box-shadow: none; }
		.img {
			transform: translateY(-50%); }
		._title {
			opacity: 0; }
		.text {
			opacity: 0; } } }

.topvalu-cate-section {
	background: #FFEFEF;
	overflow: hidden;
	padding-top: 67px;
	padding-bottom: 85px;
	.topvalu-title__ {
		margin-bottom: 60px; }
	.pattern {
		position: absolute;
		top: 0;
		left: 0;
		&._1 {
			top: auto;
			left: auto;
			right: 0;
			bottom: 0;
			transform: scale(-1); } } }

.topvalu-footer {
	padding-top: 30px;
	background: #F8F8F8;
	.logo {
		margin-bottom: 15px; }
	h4 {
		font-size: 20px;
		font-weight: 800;
		color: black;
		margin-bottom: 17px; }
	p {
		margin-bottom: 0;
		justify-content: center;
		align-items: center;
		display: flex;
		color: black;
		span {
			display: block;
			margin-left: 5px;
			margin-right: 5px;
			color: #C5C5C5; } }
	.hr {
		background: #E4E4E4;
		width: 100%;
		height: 1px;
		margin-top: 30px; }
	.end {
		padding: 16px 0;
		p {
			color: #898989;
			&:hover {
				color: black; } } } }

@media (max-width: 991.82px) {
	.topvalu-cate-box {
		margin-bottom: 30px; }
	.criterion-accordion {
		.accordion-text {
			.row {
				> * {
					margin-bottom: 10px; } } } } }
@media (max-width: 767.82px) {
	.criterion-accordion {
		h2 {
			.year {
				left: -60px;
				padding-left: 15px;
				font-size: 12px; }
			button {
				font-size: 16px;
				font-weight: bold;
				padding: 15px;
				padding-right: 30px; }
			.angle {
				right: 30px; } } }

	.criterion-accordion-list {
		padding-left: 60px;
		.criterion-accordion {
			&:first-child {
				h2:before {
					left: -60px; } }
			&:last-child {
				&:before {
					left: -60px; }
				h2:before {
					left: -60px; } } } }

	.topvalu-criterion-section {
		padding-top: 40px;
		padding-bottom: 40px; } }


@media (max-width: 575.82px) {
	.eshop-header._topvalu-ext {
		.logo {
			width: 80px; } }
	.criterion-accordion {
		.accordion-text {
			padding: 15px;
			padding-top: 0; }
		h2 {
			.angle {
				right: 20px;
				font-size: 15px; } } }
	.topvalu-product-section {
		.topvalu-title__ {
			margin-bottom: 20px; } }

	.topvalu-cate-box {
		._title {
			font-size: 13px;
			font-weight: 600; }
		.text {
			p {
				font-size: 13px; } } }

	.topvalu-footer {
		h4 {
			font-size: 18px;
			font-weight: bold; }
		p {
			flex-wrap: wrap;
			span {
				width: 100%;
				text-align: center;
				height: 0.1px;
				opacity: 0; } }
		.end {
			p {
				font-size: 13px; } } } }





