.eshop-page {
	p {
		font-size: 14px; } }
header.eshop-header {
	border-bottom: 4px solid #B41E8E;
	position: relative;
	padding-top: 10px;
	padding-bottom: 10px;
	&:before {
		position: absolute;
		content: '';
		top: 100%;
		left: 50%;
		width: 100%;
		transform: translateX(-50%);
		height: 1px;
		background: rgba(#000, .3); }
	.mobile-menu {
		ul {
			display: flex;
			flex-direction: column;
			align-items: center;
			li {
				a {
					display: block;
					text-transform: uppercase;
					font-size: 15px;
					font-weight: 600;
					padding: 10px;
					color: black; } } } } }


.eshop-header-content {
	display: flex;
	justify-content: space-between;
	align-items: center;
	ul {
		display: flex;
		li {
			margin-left: 35px;
			a {
				font-size: 15px;
				font-weight: 600;
				color: black;
				position: relative;
				text-transform: uppercase;
				&:before {
					position: absolute;
					content: '';
					top: 100%;
					left: 50%;
					width: 100%;
					transform: translateX(-50%);
					height: 1px;
					background: rgba(#000, .3);
					opacity: 0; }
				&:hover, &.active {
					&:before {
						opacity: 1; } } } } } }

.eshop-title__ {
	h3 {
		font-size: 28px;
		font-weight: 800;
		text-transform: uppercase;
		margin-bottom: 0; }
	p {
		font-size: 16px;
		margin-bottom: 0;
		span,a {
			color: #B03E91;
			font-weight: bold; } }
	@media (max-width: 991.82px) {
		h3 {
			font-size: 25px; } }
	@media (max-width: 767.82px) {
		h3 {
			font-size: 20px; } }
	@media (max-width: 575.82px) {
		h3 {
			font-weight: bold; } } }

.eshop-title__sm {
	h4 {
		font-size: 27px;
		font-weight: 800;
		margin-bottom: 0; }

	@media (max-width: 767.82px) {
		h4 {
			font-weight: bold;
			font-size: 22px; } }
	@media (max-width: 575.82px) {
		h4 {
			font-size: 18px; } } }
.eshop-banner-section {
	overflow: hidden;
	> img {
		position: relative; }
	.video-wrapper {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 1;
		.video-js {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: none;
			iframe {
				opacity: 0; }
			.vjs-poster {
				opacity: 0; }
			&.vjs-playing {
				iframe {
					opacity: 1; }
				.vjs-poster {
					opacity: 1; } } }
		.video-js .vjs-big-play-button {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 50px;
			height: 50px;
			line-height: 50px;
			border-radius: 50%; } } }

.eshop-intro-section {
	padding-top: 86px;
	padding-bottom: 20px;
	.eshop-title__ {
		margin-bottom: 90px;
		width: 868px;
		margin-left: auto;
		margin-right: auto;
		max-width: 100%;
		h3 {
			margin-bottom: 20px; } } }


.eshop-intro-block {
	.eshop-title__sm {
		margin-bottom: 22px; }

	.img {
		img {
			display: block;
			margin-right: 0;
			margin-left: auto; } }
	.text {
		width: calc(1140px / 2);
		padding-top: 40px;
		p {
			text-align: justify; } }

	&._reverse {
		.text {
			margin-right: 0;
			margin-left: auto; } }
	ul {
		li {
			white-space: nowrap;
			font-size: 15px;
			font-weight: bold;
			color: #000;
			margin-bottom: 20px;
			img {
				width: 20px;
				margin-right: 5px; } } } }
.eshop-why-row {
	margin-left: -8px;
	margin-right: -8px;
	> [class*="col-"] {
		display: flex;
		padding-left: 8px;
		padding-right: 8px; }
	@media (min-width: 992px) {
		.col-xl {
			width: 20%; } } }
.eshop-why-box {
	width: 100%;
	position: relative;
	margin-bottom: 16px;
	.img {
		display: table;
		margin-left: auto;
		margin-right: auto;
		position: relative; }
	.text {
		text-align: center;
		position: relative;
		padding: 17px;
		padding-bottom: 20px; }
	h4 {
		font-weight: bold;
		font-size: 14px;
		white-space: nowrap;
		margin-bottom: 15px; }
	&:before {
		position: absolute;
		content: '';
		bottom: 0;
		right: 0;
		width: 100%;
		height: calc(100% - 40px);
		background: #fff;
		border-radius: 10px; }
	p {
		font-family: $font-2;
		font-size: 13px;
		margin-bottom: 0;
		color: #434343; } }

.eshop-why-section {
	background: linear-gradient(113.97deg, #0574E4 1.92%, #008DF0 100.59%);
	padding: 70px 0;
	.pattern {
		position: absolute;
		top: 10px;
		left: 0;
		&._2 {
			top: auto;
			right: 0px;
			bottom: 0;
			left: auto; } }
	.container {
		@media (min-width: 1300px) {
			width: 1270px; } }

	.eshop-title__ {
		margin-bottom: 50px;
		width: 569px;
		max-width: 100%;
		margin-left: auto;
		margin-right: auto;
		h3 {
			color: #fff; } } }
.glam-members-section {
	.eshop-title__ {
		margin-bottom: 30px; } }

.eshop-video-section {
	background: #FFE7F2;
	padding-top: 76px;
	padding-bottom: 84px;
	.pattern {
		position: absolute;
		bottom: 10%;
		left: 0;
		&._2 {
			bottom: 20%;
			right: 0;
			left: auto; }
		&._3 {
			bottom: 0;
			width: 100%; }
		&._4 {
			bottom: 0;
			left: 10%;
			z-index: 2; } }

	.eshop-title__ {
		width: 710px;
		margin-left: auto;
		margin-right: auto;
		max-width: 100%;
		margin-bottom: 50px; }
	.container {
		position: relative; }
	.video-wrapper {
		width: 854px;
		max-width: 100%;
		margin-left: auto;
		margin-right: auto;
		overflow: hidden;
		box-shadow: 0 0 0 10px rgba(#FF5F83, .2);
		border-radius: 10px; } }

.eshop-now-section {
	padding-top: 80px;
	padding-bottom: 20px;
	.eshop-title__ {
		margin-bottom: 40px;
		p {
			font-size: 22px;
			color: #fff; }
		h3 {
			font-weight: 38px;
			color: #fff; } }
	a.bt, a.li {
		display: table;
		margin-left: auto;
		margin-right: auto; }
	a.bt {
		color: #000;
		font-weight: bold;
		font-size: 16px;
		padding: 10px 30px;
		background: #fff;
		border-radius: 10px;
		margin-bottom: 40px;
		transition: all .3s ease-in-out;
		&:hover {
			box-shadow: 0 3px 3px rgba(black, .3); } }
	a.li {
		color: #fff;
		font-weight: bold;
		font-size: 16px; } }

footer.eshop-footer {
	background: #F8F8F8;
	padding-top: 40px;
	padding-bottom: 25px;
	h4 {
		font-size: 16px;
		font-weight: 800;
		color: #B1238C;
		font-family: $font-2;
		height: 38px;
		text-transform: uppercase; }
	p {
		display: flex;
		font-family: $font-2;
		font-size: 15px;
		color: black;
		strong {
			font-weight: bold;
			white-space: nowrap;
			min-width: 130px; } }
	.appdownload-row {
		margin-left: -5px;
		margin-right: -5px;
		> [class*="col-"] {
			padding-left: 5px;
			padding-right: 5px; } }
	.socials {
		li {
			a {
				display: block;
				width: 40px;
				height: 40px;
				border: 1px solid #9D9D9D;
				display: flex;
				justify-content: center;
				align-items: center;
				color: #9D9D9D;
				border-radius: 50%; } } } }

@media (max-width:  1190.82px) {
	.eshop-intro-block {
		.text {
			width: 480px; } } }

@media (max-width: 991.82px) {
	.eshop-intro-block {
		margin-bottom: 40px;
		.col-text {
			order: 2; }
		.img {
			margin-bottom: 20px;
			img {
				margin-left: auto;
				margin-right: auto; } }
		.text {
			width: 720px;
			padding: 0 15px;
			margin-left: auto!important;
			margin-right: auto!important; } }

	footer.eshop-footer {
		h4 {
			height: auto; } } }


@media (max-width: 767.82px) {
	.eshop-intro-section {
		padding-top: 40px;
		.eshop-title__ {
			margin-bottom: 40px; } }

	.eshop-intro-block {
		.text {
			width: 540px; } } }

@media  (max-width: 575.82px) {
	.eshop-intro-block {
		.text {
			width: 100%; } }

	.eshop-video-section {
		.pattern._4 {
			width: 100px; } } }




